import { Feature } from "../constants/constant";
import { InitialState } from "../store/initialState";

export function SignInReducer(state = InitialState, action) {
    switch (action.type) {
                case Feature.SET_USER_NAME:
                return{
                    ...state,
                    user:{
                        ...state.user,
                        username:action.payload
                    }
                }
                case Feature.SET_PASSWORD:
                    return{
                        ...state,
                        user:{
                            ...state.user,
                            password:action.payload
                        }
                    }
                    case Feature.SET_EMAIL:
                    return{
                        ...state,
                        user:{
                            ...state.user,
                            email:action.payload
                        }
                    }

        default:
            return {
                ...state
            }

    }
}