import { Grid, TextField } from "@mui/material";
import ImageField from "../../common/imageFeild/ImageField";
import LabelField from "../../common/labelField/LabelField";
import SwitchButton1 from "../../common/SwitchButton/SwitchButton1";
import AddIcon from '@mui/icons-material/Add';
import Button from '@mui/material/Button';
import Link from '@mui/material/Link';
import Typography from '@mui/material/Typography';
import { useSelector } from "react-redux/es/exports";
import { useDispatch } from "react-redux/es/exports";
import { setUsername, setPassword, setEmail } from "../../Redux/action/Action";
import { useNavigate } from "react-router-dom";


const ButterflyImg = require("../../images/med.jpg")
const logo = require("../../images/medical.jpg")
export default function SignUp(props) {
    const user = useSelector((state) => state.feature.user);
    const dispatch = useDispatch();
    const navigate = useNavigate();

    const img = {
        src: ButterflyImg
    }
    const logo1 = {
        src: logo
    }

    const handelClick = () => {
        alert(JSON.stringify("Sign-Up Successfully"));
        const option = {
            method: "POST",
            headers: new Headers({ 'content-type': 'application/json' }),
            mode: 'cors',
            body: JSON.stringify(user)
        }

        fetch('https://fakestoreapi.com/users', option)
            .then(res => res.json())
            .then(json => {
                console.log(json);
                navigate("/pages/dashboard")
            });

    }
    const changeUsername = (e) => {
        dispatch(
            setUsername(e.target.value)
        )
    }
    const changeEmail = (e) => {
        dispatch(
            setEmail(e.target.value)
        )
    }
    const changePassword = (e) => {
        dispatch(
            setPassword(e.target.value)
        )
    }
    return (
        <>
            <Grid container spacing={4}>
                <Grid xs={12} md={4} lg={4} item>
                    <Grid container padding={5} spacing={4}>
                        <Grid style={{ paddingBottom: 40 }} xs={12} md={12} lg={12} item>
                            <ImageField image={logo1}></ImageField>
                        </Grid>
                        <Grid item xs={12} md={12} lg={12}>
                            <LabelField styles={{ fontWeight: "700" }} variant={"h5"} label={"Sign up"} ></LabelField>
                            <LabelField styles={{ color: "#8f9091", fontSize: 15 }} variant={"p"} label={"Create your Account"}></LabelField>
                        </Grid>
                        <Grid style={{ paddingTop: 18 }} xs={12} md={12} lg={12} item>
                            <TextField onChange={changeUsername} value={user.username} sx={{ width: 325, backgroundColor: "#ebebeb" }} size={"small"} label={"Name"} ></TextField>
                        </Grid>
                        <Grid style={{ paddingTop: 18 }} xs={12} md={12} lg={12} item>
                            <TextField onChange={changeEmail} value={user.email} sx={{ width: 325, backgroundColor: "#ebebeb" }} size={"small"} label={"Email Address"} ></TextField>
                        </Grid>
                        <Grid style={{ paddingTop: 18 }} xs={12} md={12} lg={12} item>
                            <TextField onChange={changePassword} value={user.password} sx={{ width: 325, backgroundColor: "#ebebeb" }} size={"small"} label={"Password"} > </TextField>
                        </Grid>
                        <Grid style={{ paddingTop: 20 }} xs={12} md={12} lg={12} item>
                            <SwitchButton1>I agree to the Terms and Privacy.</SwitchButton1>
                        </Grid>
                        <Grid style={{ paddingTop: 20 }} xs={12} md={12} lg={12} item>
                            <Button onClick={handelClick} variant="contained" startIcon={<AddIcon />}>Register</Button>
                        </Grid>
                        <Grid style={{ paddingBottom: 25 }} xs={12} md={12} lg={12} item >
                            <div style={{ fontSize: 14, marginTop: 6 }}> Have an account?
                                <Link href={"/"} underline="none">Sign In!</Link></div>
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item xs={0} md={8} lg={8} className="signup-right-panel" >
                    <ImageField image={img}></ImageField>
                </Grid>
            </Grid>


        </>
    )
}