import * as React from 'react';
import { useTheme } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import SkipPreviousIcon from '@mui/icons-material/SkipPrevious';
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import SkipNextIcon from '@mui/icons-material/SkipNext';
import MedicalServicesIcon from '@mui/icons-material/MedicalServices';

export default function MediaCard(props) {
  const theme = useTheme();

  return (
    <Card  sx={{ display: 'flex' ,height:68}}>
      <Box padding={2} sx={{ display: 'flex', flexDirection: 'column' }}>
        <CardContent>
          <MedicalServicesIcon sx={{color:"blue",fonySize:"large",paddingBottom:7}}
          ></MedicalServicesIcon>
         </CardContent>
      </Box>
      <Box sx={{  flexDirection: 'column' }} >
        <Typography sx={{fontWeight:600,fontSize:14 }} >Appointment</Typography>
        <Typography padding={1} sx={{color:"blue",fontWeight:400,fontSize:22 }}>216</Typography>
        </Box>
    
    </Card>
  );
}