import { Grid, TextField } from "@mui/material";
import ImageField from "../../common/imageFeild/ImageField";
import LabelField from "../../common/labelField/LabelField";
import SwitchButton from "../../common/SwitchButton/SwitchButton";

import Button from '@mui/material/Button';
import { Link } from "react-router-dom";
import Typography from '@mui/material/Typography';
import ButtonField from "../../common/ButtonField/ButtonField";
import { useSelector } from "react-redux/es/exports";
import { useDispatch } from "react-redux/es/exports";
import { setUsername, setPassword } from "../../Redux/action/Action";
import { useNavigate } from "react-router-dom";



const medic2 = require("../../images/mountains.jpg")
const logo = require("../../images/medical.jpg")

export default function SignIn(props) {
  const user = useSelector((state) => state.feature.user);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const medic1 = {
    src: medic2
  }
  const logo1 = {
    src: logo
  }

  const onClickHandler = () => {
    alert(JSON.stringify("Login Successfully"))

    const option = {
      method: "POST",
      headers: new Headers({ 'content-type': 'application/json' }),
      mode: 'cors',
      body: JSON.stringify(user)
    }

    fetch('https://fakestoreapi.com/users', option)
      .then(res => res.json())
      .then(json => {
        console.log(json);
        navigate("/pages/dashboard")
      });

  }

  const changeUsername = (e) => {
    dispatch(setUsername(e.target.value))
  }
  const changePassword = (e) => {
    dispatch(setPassword(e.target.value))
  }
  return (
    <>
      <Grid container spacing={2}>
        <Grid xs={12} md={4} lg={4} item>
          <Grid container padding={5} spacing={5}>
            <Grid style={{ paddingBottom: 40 }} xs={12} md={12} lg={12} item>
              <ImageField image={logo1}></ImageField>
            </Grid>
            <Grid item xs={12} md={12} lg={12}>
              <LabelField styles={{ fontWeight: "700" }} variant={"h5"} label={"Login-Form"} ></LabelField>
              <LabelField styles={{ color: "#8f9091", fontSize: 15 }} variant={"p"} label={"Login to access your Account"}></LabelField>
            </Grid>
            <Grid style={{ paddingTop: 15 }} xs={12} md={12} lg={12} item>
              <TextField onChange={changeUsername} value={user.username} sx={{ width: 325, backgroundColor: "#ebebeb" }} size={"small"} label={"Username"} ></TextField>
            </Grid>
            <Grid style={{ paddingTop: 18 }} xs={12} md={12} lg={12} item>
              <TextField onChange={changePassword} value={user.password} sx={{ width: 325, backgroundColor: "#ebebeb" }} size={"small"} label={"Password"} > </TextField>
            </Grid>
            <Grid style={{ paddingTop: 20 }} xs={12} md={12} lg={12} item>
              <SwitchButton ></SwitchButton>
            </Grid>
            <Grid style={{ paddingTop: 20 }} xs={12} md={12} lg={12} item>
              <ButtonField onClickHandler={onClickHandler} variant={"contained"} name={"Login"} ></ButtonField>
            </Grid>
            <Grid style={{ marginTop: 15 }} xs={12} md={12} lg={12} item >
              <Typography style={{ fontSize: 14 }}><Link to={"/pages/ForgotPsw"} underline="none">Forgot Password?</Link></Typography>
              <Typography style={{ fontSize: 14 }}>Don't have an account?
              <Link to={"/pages/SignUp"} underline="none">Sign up!</Link>
              </Typography>
             
            </Grid>
          </Grid>
        </Grid>
        <Grid xs={0} md={8} lg={8} item className="login-right-panel">
          <ImageField image={medic1}></ImageField>
        </Grid>
      </Grid>


    </>
  )
}